/* RESET GLOBAL */
* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  /* La fuente de Figma */
  font-family: 'Roboto', sans-serif;
  background-color: #ffffff;
}

/* CONTENEDOR PRINCIPAL */
#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
}

/* TIPOGRAFÍAS */
h1,
h2,
h3,
h4,
p {
  margin: 0;
}

/* ESTILOS DE FORMULARIO (Globales para la página Home) */
input,
button {
  font-family: inherit;
}

/* Utility classes si las necesitás */
.container-960 {
  max-width: 960px;
  margin: 0 auto;
  width: 100%;
  padding: 0 20px;
}

.header-title {
  font-size: 52px;
  line-height: 0.85;
  /* Estrecha el espacio entre "Mis" y "pendientes" */
  margin: 30px 0;
  font-weight: 800;
  max-width: 200px;
  /* Esto fuerza el salto de línea como en la foto */
}